import React from 'react'
import styles from './Home.module.css'
import MessageLoader from '../../components/MessageLoader'
import BarcodeScanner from '../../components/BarcodeScanner'
import QrCodeScanner from '../../components/qrcode/QrCodeScanner'

/**
 * Composant reprsentant la page d'accueil de l'application RHnet.
 *
 * @function
 * @returns {JSX.Element} - L'lment JSX de la page d'accueil.
 */
const Home = (decodedText, decodedResult) => {
  const [scannedCode, setScannedCode] = React.useState(null)
  const handleDecodedData = (decodedData) => {
    console.log('QR Code decoded:', decodedData)
    setScannedCode(decodedText)
  }

  return (
    <main className={styles.homeContainer}>
      <h1>RHnet</h1>
      <MessageLoader />
      <BarcodeScanner />
      <QrCodeScanner onDecodedData={handleDecodedData} />
      {/* Affichez le numro du code QR scann ici, si ncessaire */}
      {scannedCode && <p>Numro du QR code scann : {scannedCode}</p>}
    </main>
  )
}

export default Home
